html {
  --text-nav-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  display: block;
}

.nav-ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav-li {
  margin-left: 20px;
}

.nav-li:first-of-type, .nav-link:first-of-type {
  margin-left: 0;
}

.nav-link:hover, .is-active {
  color: var(--text-nav-color);
}

.is-active:after {
  content: "";
  background-color: var(--text-nav-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
